<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('CLASS_NAME') }}</label>
          <vs-input class="w-full" v-model="description"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('PARENT_CLASS') }}</label>
          <party-class-lookup :title="$t('PARENT_CLASS')" valueField="partyClassificationId" displayField="description" @select-record="selectPartyClassRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('CLASS_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="classificationTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in classificationTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('STANDARD_CODE') }}</label>
          <vs-input class="w-full" v-model="standardCode"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="createPartyClass('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="createPartyClass('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="createPartyClass('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import PartyClassLookup from '@/views/nucleus/party-class/PartyClassLookup'
import PartyClassService from '@/services/nucleus/PartyClassService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'party-class-create',
  mixins: [AlertMixin],
  components: {
    PartyClassLookup
  },
  data: function () {
    return {
      parentClassificationId: '',
      description: '',
      standardCode: '',
      classificationTypes: [],
      classificationTypeEnumId: 'PcltCustomer'
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'PartyClassificationType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.classificationTypes = response.data.enumerationList || []
      })
    },
    selectPartyClassRecord (record) {
      this.parentClassificationId = record.partyClassificationId || ''
    },
    createPartyClass (nextAction) {
      let self = this
      PartyClassService.createPartyClass({
        parentClassificationId: this.parentClassificationId,
        description: this.description,
        standardCode: this.standardCode,
        classificationTypeEnumId: this.classificationTypeEnumId
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewPartyClass', params: { id: response.data.partyClassificationId } })
          } else {
            self.$router.push({ name: 'BrowsePartyClasses' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
